
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import { validateNotNull, validateUpload, validateInt1, validateInt } from 'util/rules'
import Uploader from 'assembly/uploader/uploader'
import Dropdown from 'assembly/dropdown/dropdown'

export default {
data () {
return {
baseUrl: process.env.FMS_SERVICE_IP,
asTypeSelected: false,
asTypeMenu: {
'1': '多发',
'2': '错发',
'3': '漏发',
'4': '残次申报',
'6': '质量评估申报'
},
aSType: '多发',
type: '1',
tip: '',
itemlist: [],
orderNo: '',
declarationProductNo: '',
declarationProductName: '',
declarationUnit: '套',
declarationQuantity: 0,
description: '',
photos: ['', '', '', '', ''],
pickSlipNo: '',
containerNo: '',
weight: '',
receivedDate: '',
realityNumber: 0,
listNum: 0,
nodatatext: '',
placeholder: '请输入产品编号或产品名称',
backExpressCompany: '',
backExpressNo: '',
img1: require('../../assets/images/wlbq.jpeg'),
img2: require('../../assets/images/zlpm.jpeg'),
img3: require('../../assets/images/xdnr.jpeg'),
img4: require('../../assets/images/zxcz.jpeg'),
img5: require('../../assets/images/wzqt.jpeg'),
img6: require('../../assets/images/cpps.jpeg'),
img7: require('../../assets/images/cpqx.jpeg'),
img8: require('../../assets/images/kdth.jpeg'),
img9: require('../../assets/images/wxqk.jpeg'),
image: '',
photoText: '请提供5张现场收货照片',
photoLabel: [],
photoArr1: [
{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '多发实物',
img: ''
}, {
text: '整箱称重',
img: 'img4'
}
],
photoArr2: [{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '错发实物',
img: ''
}, {
text: '整箱称重',
img: 'img4'
}],
photoArr3: [{
text: '物流标签(正面)',
img: 'img1'
}, {
text: '重量喷码(背面)',
img: 'img2'
}, {
text: '箱单内容(箱内)',
img: 'img3'
}, {
text: '整箱完整全图',
img: 'img5'
}, {
text: '整箱称重',
img: 'img4'
}],
photoArr4: [{
text: '产品破损照片',
img: 'img6'
}, {
text: '整箱完整全图',
img: 'img5'
}, {
text: '物流标签(正面)',
img: 'img1'
}, {
text: '补充照片',
img: ''
}],
photoArr6: [{
text: '产品缺陷照片',
img: 'img7'
}, {
text: '补充照片',
img: ''
}, {
text: '补充照片',
img: ''
}, {
text: '补充照片',
img: ''
}],
photoArr: [{
text: '产品退回快递单照片',
img: 'img8'
}],
dialogVisible: false,
aSTypeOne: true,
selectType: '0',
inited: false 
}
},
methods: {
showaSTypeSelector () {
this.asTypeSelected = true
},
setList (num) {
this.listNum = num
},
showImg (img) {
this.image = this[img]
this.dialogVisible = true
},
goBack () {
this.$router.push({
path: '/afterSale'
})
},
setType (type) {
if (Number(type) === 1) {
this.aSType = '多发'
this.tip = '请说明多发成什么产品，您是否愿意购买等情况'
}
if (Number(type) === 2) {
this.aSType = '错发'
this.tip = '请说明错发成什么产品，您是否愿意购买等情况'
}
if (Number(type) === 3) {
this.aSType = '漏发'
this.tip = '请说明漏发产品情况'
}
if (Number(type) === 1 || Number(type) === 2 || Number(type) === 3) {
this.aSTypeOne = true
this.photoText = '请提供5张现场收货照片'
}
if (Number(type) === 4) {
this.aSType = '残次申报'
this.aSTypeOne = false
this.photoText = '请提供4张现场收货照片'
this.tip = '请填写破损情况'
}
if (Number(type) === 6) {
this.aSType = '质量评估申报'
this.aSTypeOne = false
this.photoText = '请提供4张照片'
this.tip = '请填写产品质量问题情况'
}
this.photoLabel = this['photoArr' + type]
},
beforeSend (file) {
if (validateUpload(this, file)) {
this.$vux.loading.show({
text: '图片上传中，请稍候'
})
return true
}
},
imgClick (index) {
this.ImgNum = index
},
onFinish (res) {
this.$vux.loading.hide()
this.photos.splice(this.ImgNum, 1, res.model)
},
onError () {
this.$vux.loading.hide()
this.$vux.toast.text('上传失败，请重试~')
},
setAsType (key, item) {
this.type = key
this.aSType = item
this.photos = ['', '', '', '', '']
this.setType(key)
},
submit () {
if (this.inited) {
this.$vux.toast.text('拼命请求中，请稍候~')
return
}
let flag = false
if (this.aSTypeOne) {
flag = validateNotNull(this, this.orderNo, '订单编号') && validateNotNull(this, this.declarationProductNo, '申报产品编号') && validateNotNull(this, this.declarationProductName, '申报产品名称') && validateInt1(this, this.declarationQuantity, '申报数量') && validateNotNull(this, this.declarationUnit, '申报单位') && validateNotNull(this, this.pickSlipNo, '拣货单号') && validateNotNull(this, this.containerNo, '所在装箱号') && validateNotNull(this, this.receivedDate, '到货日期') && validateInt(this, this.realityNumber, '实收数量') && validateNotNull(this, this.weight, '重量') && validateNotNull(this, this.description, '情况说明')
}
if (Number(this.type) === 4) {
flag = validateNotNull(this, this.orderNo, '订单编号') && validateNotNull(this, this.declarationProductNo, '申报产品编号') && validateNotNull(this, this.declarationProductName, '申报产品名称') && validateInt1(this, this.declarationQuantity, '申报数量') && validateNotNull(this, this.declarationUnit, '申报单位') && validateNotNull(this, this.pickSlipNo, '拣货单号') && validateNotNull(this, this.receivedDate, '到货日期') && validateNotNull(this, this.description, '情况说明')
}
if (Number(this.type) === 6) {
flag = validateNotNull(this, this.declarationProductNo, '申报产品编号') && validateNotNull(this, this.declarationProductName, '申报产品名称') && validateInt1(this, this.declarationQuantity, '申报数量') && validateNotNull(this, this.declarationUnit, '申报单位') && validateNotNull(this, this.description, '情况说明')
}
if (flag) {
let lastPhoto
let temPhotos = []
this.photos.map((item, index) => {
temPhotos.push(item)
})
if (Number(this.type) === 4 || Number(this.type) === 6) {
lastPhoto = temPhotos[4]
temPhotos.pop() 
}
temPhotos = temPhotos.filter(item => {
return item
})
if (temPhotos.length !== this.photoLabel.length) {
this.$vux.toast.text(this.photoText)
return false
}
temPhotos.push(lastPhoto)
this.containerNo = Number(this.type) === 4 || Number(this.type) === 6 ? '' : this.containerNo
this.receivedDate = Number(this.type) === 6 ? '' : this.receivedDate
this.realityNumber = Number(this.type) === 4 || Number(this.type) === 6 ? '' : this.realityNumber
this.weight = Number(this.type) === 4 || Number(this.type) === 6 ? '' : this.weight
let data = {
backExpressCompany: this.backExpressCompany,
backExpressNo: this.backExpressNo,
receivedDate: this.receivedDate,
containerNo: this.containerNo,
declarationProductName: this.declarationProductName,
declarationProductNo: this.declarationProductNo,
declarationQuantity: this.declarationQuantity,
declarationUnit: this.declarationUnit,
description: this.description,
orderNo: this.orderNo,
photos: this.photos,
pickSlipNo: this.pickSlipNo,
realityNumber: this.realityNumber,
type: this.type,
weight: this.weight
}
this.inited = true
this.$tupHttp(this, 'front/declaration/createDeclarationWithoutOrder', 'POST', data).then(res => {
let thiz = this
this.$vux.confirm.show({
content: '申请已成功提交，正在审核中，可以继续新建下一条申请',
confirmText: '新建下一条',
cancelText: '返回售后申请',
onCancel () {
thiz.$router.go(-1)
},
onConfirm () {
thiz.$router.go(0)
}
})
}, () => {
this.inited = false
}).catch(() => {
this.inited = false
})
}
},
_getItemData (str) {
let data = {
pagingQuery: {
pageIndex: 1,
pageSize: 50
},
isParts: Number(this.selectType),
search: str,
type: 2
}
if (Number(this.type) === 6) {
data.isKitProduct = 0
}
this.$tupHttp(this, 'front/product/selectProductBypCodeOrpName', 'POST', data).then(res => {
this.itemlist = []
res.data.models.map((item, index) => {
let info = { pCode: item.pCode, pName: item.pName }
this.itemlist.push(info)
})
console.log(this.itemlist)
if (res.data.models.length === 0) {
this.nodatatext = res.data.message
}
})
},
itemClick (data) {
this.declarationProductNo = data.pCode
this.declarationProductName = data.pName
this.placeholder = data.pCode
this.itemlist = []
this.nodatatext = ''
},
searchVals (val) {
this.placeholder = val
this.declarationProductNo = val
}
},
created () {
this.setType('1')
document.addEventListener('click', function (e) {
let sp = document.getElementById('myPanel')
let ul = document.getElementById('myUl')
let te = document.getElementById('myTe')
if (sp) {
if (!sp.contains(e.target)) {
ul.style.display = 'none'
te.style.display = 'none'
}
}
})
},
components: {
XInput,
Group,
XButton,
XHeader,
Datetime,
Cell,
Actionsheet,
XTextarea,
Uploader,
CellFormPreview,
InlineXNumber,
XNumber,
Dropdown,
XDialog
}
}



